.form > *, .form-add-video-image > * {
  display: flex;
  justify-content: space-between;
}

.form, .form-add-video-image {
  z-index: 4;
  position: absolute;
  left: 3vw;
  top: 250px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 500px;
  height: 270px;
  background-color: rgba(241, 203, 170, 0.89);
  padding: 30px;
}

.form input, .form-add-video-image input {
  padding-left: 5px;
  font-size: 1.5rem;
  font-weight: 200;
  color: #413b3D;
  width: 380px;
  margin-left: 5px;
  line-height: 2rem;
  border: none;
  background-color: #eee1d66b;
  border-bottom: 1px solid #413B3D;
}

.form label, .form-add-video-image label {
  font-size: 1.5rem;
  padding-bottom: .3rem;
}

@media (max-width: 767px) {
  .form {
    padding-top: 65px; 
    width: 300px;
    height: 225px;
    top: 110px;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
  }

  .form input {
    width: 285px;
    margin-bottom: 10px;
  }
}